.Navbar-wrapper {
  width: 100%;
  height: 88px;
  background-color: white;
  display: flex;
  justify-content: center;
}
.navigation-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-width: 800px;
  width: 1330px;
  height: 100%;
  max-width: 1330px;
}
.menu-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.logo {
  width: 200px;
}
.menu-text {
  font-family: "Markazi text";
  font-weight: 600;
  font-size: 20px;
  line-height: 24px;
  /* identical to box height */
  color: #000000;
  margin-left: 45px;
  cursor: pointer;
}
.shopping-icon {
  margin-left: 45px;
  font-size: 25px;
}
.order {
  background-color: black;
  border-radius: 12px;
  color: white;
  padding: 6px;
  font-weight: 200;
}
